primer(5, 3);

//2
function pr(A, B) {
    let count = 0;
    for (let i = B - 1; i > A; i--) {
        console.log(i);
        count += 1;
    }
    console.log("Количество чисел:", count);
    }
console.log("Задание 2:")
pr(2, 7);

//3
function pri(A, B) {
    let product = 1;
    for (let i = A; i <= B; i++) {
      product *= i;
    }
    return product;
    }
console.log("Задание 3:")
console.log("Произведение чисел от 3 до 5:", pri(3, 5));

//4
function prim(N) {
    let sum = 0;
    for (let i = 1; i <= N; i++) {
        sum += 1 / i;
    }
    return sum;
}
console.log("Задание 4:")
console.log("Сумма дробей:", prim(2));

//5
function prime(price) {
    for (let i = 1; i <= 10; i++) {
      console.log(`${i} кг конфет: ${price * i} рублей`);
    }
}
console.log("Задание 5:")
prime(100);